<template>
    <div v-if="message" @click="onClick">
        <button class="option">{{ message }}</button>
    </div>
</template>

<script>
export default {
    name: "Option",
    props: {
        index: {
            type: Number,
        },
        message: {
            type: String,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
    },
    methods: {
        onClick() {
            if (!this.disabled){
                this.$store.state.userData.questions.push(this.index)
                this.$store.dispatch("nextStage");
            }

        },
    },
};
</script>
